.submit-proposal {
    width: 50%;
    padding-left: 30px;

    .title {
        font-size: 40px;
        padding-bottom: 10px;
        //border-bottom: 2px solid $color-light-gray;
    }

    .header {
        display: flex;
        flex-direction: row;
        gap: 20px;
    }

    .success-label {
        color: #3cef00;
        margin-bottom: 15px;
    }

    .error-label {
        color: #ff0000;
        margin-bottom: 15px;
    }

    .container {
        label {
            font-weight: bold;
            //text-transform: uppercase;
            //color: $color-dark-gray;
        }

        .input-elem {
            border: 2px solid $color-border-grey;
            border-radius: 12px;
            padding: 12px 16px;
            margin-bottom: 15px;
            margin-top: 5px;
            width: 100%;
            //max-width: 605px;
        }

        #description {
            height: 150px;
        }

        #deposit,
        #amount {
            margin-top: 5px;
            border: 2px solid $color-border-grey;
            border-radius: 12px;
            background-color: $color-white;
            margin-bottom: 15px;

            .coin-items {
                display: flex;
                height: 44px;
            }
        }

        #proposal-tabs {
            border-radius: 12px;
            margin-top: 5px;
            border: 2px solid $color-border-grey;
            background-color: $color-white;
            margin-bottom: 50px;

            .tab-panel-content {
                margin: 20px 10px;
            }
        }

        .btn-submit-proposal {
            border: 2px solid $color-light-gray;
            border-radius: 6px;
            padding: 5px;
            margin-top: 5px;
            width: 100%;

            &:hover {
                cursor: pointer;
                background-color: $color-light-gray;
            }
        }
    }

    .cps-proposal {
        #recipient {
            margin-top: 5px;
            margin-bottom: 15px;
        }
    }

    .param-change-proposal {
        #change-form {
            margin-top: 5px;
            margin-bottom: 15px;

            .change-table-list {
                table {
                    border: 2px solid $color-light-gray;
                    text-align: left;
                    width: 100%;

                    td {
                        min-width: 25px;
                        vertical-align: top;
                    }

                    tr {
                        &:nth-child(odd) {
                            background: white;
                        }

                        &:nth-child(even) {
                            background: $color-gray-background;
                        }
                    }
                }
            }
        }
    }

    .parameter-change-form {
        .input-params {
            display: flex;
            margin-bottom: 15px;

            .input-param-elem {
                width: 100%;

                .value-input {
                    border: 1px solid hsl(0, 0%, 80%);
                    border-radius: 4px;

                    width: 100%;
                    height: 100%;
                }
            }

            .btn-add-param {
                border: 1px solid hsl(0, 0%, 80%);
                border-radius: 4px;
            }
        }
    }

    .change-item {
        display: flex;
    }

    .coins-form {
        //width: ;
        //border-radius: 6px;
        display: flex;
        flex-direction: column;
        gap: 5px;

        input {
            border: 2px solid $color-border-grey;
            border-radius: 6px;
            padding: 5px;
        }

        .btn-add-coin {
            border: 2px solid $color-light-gray;
            border-radius: 6px;
            padding: 5px;

            &:hover {
                cursor: pointer;
                background-color: #fbfbfb;
            }
        }
    }

    .coin-row {
        display: flex;
        flex-direction: row;
        gap: 4px;
        margin-left: 4px;
        margin-top: 4px;
    }

    .btn-delete {
        width: 20px;
        text-align: center;
        font-weight: bold;
        color: black;
        border-radius: 6px;

        &:hover {
            background-color: #ffb7b7;
            color: #ff4141;
        }
    }

    .coin-item {
        display: flex;
        background-color: $color-light-gray;
        margin: 5px;
        padding: 5px;
        border-radius: 6px;

        font-size: 15px;
        //font-weight: bold;
        color: $color-dark-gray;

        .btn-delete {
            padding: 0 5px;
            margin-left: 3px;
        }
    }

    .plan-form {
        &__label {
            display: flex;
            flex-direction: row;
            gap: 10px;
            align-items: center;
        }

        &__input {
            @extend .input-elem;
        }
    }
}
